/* ==========================
   Desktop Style Reset
   ========================== */
* {
  -webkit-touch-callout: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

html,
body {
  -ms-overflow-style: scrollbar;
}

html {
  width: 100%;
  height: 100%;
}

body {
  .g-in;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  background-color: @bg-base;
  color: @text-color;
  font-size: @font-size;
  line-height: 120%;
  font-family: @font-family;
  min-width: @width-mini;
  font-weight: 400;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: @font-family-title;
  line-height: 120%;
  font-weight: normal;
  margin: 0;
  padding: 0;
  color: @header-color;
}

a {
  cursor: pointer;
  text-decoration: none;
  outline: none;
  color: @link-color;

  &:hover {
    color: @link-color-dark;
  }

  &:focus {
    text-decoration: none;
    outline: dotted thin;
    outline: -webkit-focus-ring-color auto 5px;
    outline-offset: -2px;
  }
}

p {
  margin: 0 0 @space;
  line-height: 1.5;
  font-size: @font-size;
}

*+p,
*+ul,
*+ol,
*+dl,
*+pre,
*+address,
*+fieldset,
*+figure {
  margin-top: @space;
}

div,
ul,
ol,
li,
a {
  .g-in;
}

/*
 * code is inline block, not block
 */
pre {
  border: none;
  width: 100%;
  overflow-x: auto;
  margin: 0;
  .inside;
  box-sizing: border-box;
}
